        // treatments Modal
        $('#treatmentsModal').on('show.bs.modal', function (e) {
            var treatment = e.relatedTarget.innerHTML;
            var img = `img/treatments/${treatment}.jpg`;
            $.ajax({
                url: img, //or your url
                success: function (data) {
                    $('#treatmentsModal img').attr("src", img);
                    $('#treatmentsModal img').attr("alt", treatment + ", tratamiento en LBN Odontologia, consultorio odontologico en Buenos Aires, Argentina");
                    return img;
                },
                error: function (data) {
                    var img = 'img/treatments/logo_lbn_consultorio.jpg';
                    $('#treatmentsModal img').attr("src", img);
                    $('#treatmentsModal img').attr("alt", treatment + ", tratamiento en LBN Odontologia, consultorio odontologico en Buenos Aires, Argentina");
                },
            })

            $('#flotante').hide("slow")

        })

        $('#treatmentsModal').on('hide.bs.modal', function (e) {
            $('#flotante').show("slow")
        })

        // Modal Banner Promotion
        setTimeout(function(){
            $('#bannerModal').modal()
        }, 1200)


        $('#bannerModal').on('hidden.bs.modal', function (e) {
            $('#bannerModal').modal('dispose');
        })

