import $ from 'jquery'
import { useEffect } from 'react'
import Specialties from '../specialties';
import Technology from '../technology';


const Main = () => {

    useEffect(() => {

        if (window.screen.width > 768) {
            $('main').removeClass('container-fluid')
            $('main').addClass('container')
            $('#accordionSpecialities').hide()
        } else {
            $('#navSpecialities').hide()
        }


        // Color-today
        var today = new Date(Date.now()).getDay()
        var idToday = "#day-" + today
        $(idToday)[0].style.color = "#83C7D3";

        $(window).resize(function () {

            if (window.innerWidth > 768) {
                $('main').removeClass('container-fluid')
                $('main').addClass('container');
                $('#accordionSpecialities').hide();
                $('#navSpecialities').show();
                // $('.carousel-caption').eq(1).css("backgroud","red");
                $('#social-net a').each(function (index, value) {
                    if ($(value).has('col-4')) {
                        $(value).removeClass('col-4')
                        $(value).addClass('col-2')
                        if (index === 0) {
                            $(value).addClass('offset-3')
                        }
                    }
                })
            } else {
                $('main').addClass('container-fluid');
                $('main').removeClass('container');
                $('#accordionSpecialities').show();
                $('#navSpecialities').hide();
                $('#social-net a').each(function (index, value) {
                    if ($(value).has('col-2')) {
                        $(value).removeClass('col-2')
                        $(value).addClass('col-4')
                        if (index === 0) {
                            $(value).removeClass('offset-3')
                        }
                    }
                })
            }
        })
    }, [])


return (
    <main className="container-fluid"> 
        <Specialties/>
        <Technology/>
    </main>
    )
}

export default Main