import { useEffect, useState } from 'react'
import $ from 'jquery'
import { categories, specialties } from '../../data'

const Specialties = () => {

    const [smallDevice, setSmallDevice] = useState(null)
    const checkDeviceWidth = () => {
        if (window.innerWidth < 768) {
            setSmallDevice(true)
        } else {
            setSmallDevice(false)
            $('#v-pills-tab:first-child a').first().tab('show')
        }
    }

    useEffect(() => {
        checkDeviceWidth()
        $('#v-pills-tab:first-child a').first().tab('show')
        window.addEventListener('resize', ()=>checkDeviceWidth())
    }, [])


    return (
        <section id={categories[0]}>
            <h4>{categories[0]}</h4>
            <h6>
            Nuestro equipo está integrado por odontólogos especializados en todas las áreas, lo que nos permite ofrecerte una atención multidisciplinaria, asegurando que todas tus necesidades puedan ser atendidas en el mismo lugar.
            </h6>

            {
                smallDevice ? (
                    console.log(1),
                    <div className="accordion" id="accordionSpecialities">
                        {
                            specialties.map((sp,index)=>{
                                return (

                                    <div key={sp+index} className="card">
                                        <div className="card-header" id={`card-${sp.name}`}>
                                            <h5 className="mb-0">
                                                <button className="btn btn-link" type="button" data-toggle="collapse"
                                                    data-target={`#collapse${index}`} aria-expanded="true" aria-controls="collapseOne">
                                                    {sp.name}
                                                </button>
                                            </h5>
                                        </div>

                                        <div id={`collapse${index}`} className="collapse" aria-labelledby="headingOne"
                                            data-parent="#accordionSpecialities">
                                            <div className="card-body">
                                                <p> {sp.text} </p>

                                                {
                                                (sp.treatments.length > 0) && (

                                                <div className="">
                                                    <div className="treatments">
                                                        Tratamientos:
                                                    </div>
                                                    <ul>
                                                        {
                                                        sp.treatments.map((tr,index)=>
                                                        <li key={tr+index} className="btn btn-link" type="button" data-toggle="modal"
                                                            data-target="#treatmentsModal">{tr}</li>
                                                        )
                                                        }

                                                    </ul>
                                                </div>

                                                )

                                                }


                                            </div>
                                        </div>
                                    </div>
                                )
                            })
                        }
                    </div>
                )
                :  (
                    console.log(2),
                    <div id="navSpecialities">
                        <div className="row">
                            <div className="col-3">
                                <div className="nav flex-column nav-pills" id="v-pills-tab" role="tablist"
                                    aria-orientation="vertical">
                                    {
                                        specialties.map((el,index)=>
                                        <a key={el+index} className={`btn btn-link`} id={`v-pills-${index}-tab`}
                                        data-toggle="pill" href={`#v-pills-${index}`} role="tab"
                                        aria-controls={`v-pills-${index}`} aria-selected="true">
                                        {el.name}
                                    </a>
                                    )
                                }
                                </div>
                            </div>
                            <div className="col-9 specialties-content">
                                <div className="tab-content" id="v-pills-tabContent">
                                    {
                                        specialties.map((el, index)=>{
                                            return (
                                    <div key={el+index} className="tab-pane fade" id={`v-pills-${index}`} role="tabpanel"
                                        aria-labelledby={`v-pills-${index}-tab`}>
                                        <p className='specialties-text'>{el.text}</p>
                                        {
                                            (el.treatments.length > 0) &&
                                            <div className="">
                                                <div className="treatments">
                                                    Tratamientos:
                                                </div>
                                                <ul>
                                                    {
                                                        el.treatments.map((el,ind)=>
                                                        <li key={el+ind} className="btn btn-link" type="button" data-toggle="modal"
                                                        data-target="#treatmentsModal">{el}</li>
                                                        )
                                                    }
                                                </ul>
                                            </div>
                                        }
                                    </div>
                                    )})
                                }
                                </div>
                            </div>
                        </div>
                    </div>
                ) 
            }


        </section>
    )
}

export default Specialties